@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,600;1,400&display=swap);

.checkbox {
    padding: 10px 70px 10px 50px;
    position: relative;
    display: inline-block;
    width: 100%;
    text-transform: none;
    font-weight: 400;
    font-size: 13px;
}

.checked {
    position: absolute;
    width: 20px;
    height: 20px;
    margin-left: -30px;
    background-image: url(/static/media/unchecked.ad129cbf.svg);
}

.input:checked+.checked {
    background-image: url(/static/media/checked.dde60505.svg);
}

.checkbox:hover {
    background: #F1FCFF;
    cursor: pointer;
}
.switch {
    position: relative;
    display: inline-block;
    width: 100%;
    text-transform: none;
    border: 1px solid #DFE5EC;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
}

.button {
    position: relative;
    display: inline-block;
    width: calc((100% - 1px)/2); 
    padding: 15px;
    background-color: #FFFFFF;
    text-align: center;
    font-size: 12px;
    line-height: 20px;
}
.left {
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
}
.right {
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
}
.input:checked+.button {
    background-color: #2196F3;
    width: calc((100% - 1px)/2 + 1px); 
    color:#FFFFFF;
}

.button:hover {
    cursor: pointer;
}
.headerWrapper {
    display: flex;
    flex-direction: row;
    margin: 20px 10px 10px 10px;
}
.price {
    flex: 1 1;
    margin: 6px 10px 10px 10px;
    font-size: 24px;
    line-height: 24px;
    color: #2196F3;
}
.headerWrapper span {
    flex: 1 1;
    margin: 0px 10px 0px 10px;
}
.logo {
    margin: auto auto auto 0;  
}

.infoWrapper {
    display: flex;
    flex-direction: row;
    margin: 0px 10px 0px 10px;
}
.column {
    margin: 5px 10px 5px 10px;
    flex: 1 1;
}

.infoBlockWrapper {
    display: inline-block;
    width: 100%;
    /* display: flex;
    flex-direction: column; */
}
.row1 {
    /* flex: 1; */
    color: #A0B0B9;
    font-size: 12px;
    line-height: 18px;
}
.row2 {
    /* flex: 1; */
    color: #4A4A4A;
    font-size: 14px;
    line-height: 21px;
    text-transform: none;
}
.ticket {
    position: relative;
    border: 1px solid #DFE5EC;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    background: #FFFFFF;
    margin: 10px 103px 10px 10px;
     display: inline-block;
    width: 502px;
    height: 184px; 
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
* {
    box-sizing: border-box;
    text-transform: uppercase;
}

.unselectable {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none;   /* Chrome/Safari/Opera */    /* Konqueror */      /* Firefox */
    -ms-user-select: none;       /* Internet Explorer/Edge */
    user-select: none;           /* Non-prefixed version, currently
                                    not supported by any browser */
  }

  .input {}
  
  .visually-hidden {
      position: absolute;
      width: 1px;
      height: 1px;
      margin: -1px;
      border: 0;
      padding: 0;
      clip: rect(0 0 0 0);
      overflow: hidden;
  }

.app {
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.5px;
    width: 960px;
    height: 1350px;
    background: #F3F7FA;
    margin: auto;
}

.app-wrapper {
    display: grid;
    grid-template-areas: "Filter Tab" "Filter Tickets";
}

.appHeader {
    text-align: center;
    padding-top: 39px;
    padding-bottom: 32px;
}

.filter {
    margin: 0px 10px 10px 103px;
    grid-area: Filter;
    background: #FFFFFF;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
     display: inline-block;
    width: 232px;
    height: 252px; 
}
.filter h1 {
    margin: 20px;
    font-weight: 600;
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 0.5px;
}

.tab {
    margin: 0px 102px 10px 10px;
    grid-area: Tab;
    display: inline-block;
    width: 503px;
    height: 50px; 
}

.tickets {
    grid-area: Tickets;
}
